import { Component,Inject} from '@angular/core';
import { JsonCreate } from 'src/app/services/jsonCreate.service';
import { trigger, state, style, transition, animate } from '@angular/animations';
import { MatDialog,MatDialogRef,MAT_DIALOG_DATA} from '@angular/material/dialog';


export interface DialogData {
  collections: string;
  name: string;
  
}

@Component({
  selector: 'app-dnevnik-read',
  animations: [
    trigger('changeDivSize',[
      state('initial', style({
      })),
      state('final', style({
      })),
      transition('initial<=>final', animate('200ms')),
      transition('final=>initial', animate('200ms'))
      
    ])
  ],
  templateUrl: './dnevnik-read.component.html',
  styleUrls: ['./dnevnik-read.component.sass'],
  
})
export class DnevnikReadComponent  {
  jsonData: any
  collections: string;
  name: string;

  
  
  // isClick = false

  constructor(private js: JsonCreate, public dialog: MatDialog) {
    this.js.getConfig().subscribe(value => {
      this.jsonData = value
      console.log(value)
    })

    
  }
  openDialog(i: number): void {
    console.log(this.collections)
    console.log(this.jsonData[i].message)
    const dialogRef = this.dialog.open(DialogOverviewExampleDialog, {
      width: '450px',
      data: { 
        name: this.jsonData[i].title,
        collectios: this.jsonData[i].message,}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });


  }

//    public changeState(index: number): void {
//     console.log(index)
//     this.jsonData[index].currentState = 
//         (this.jsonData[index].currentState  === 'initial') ? 'final' : 'initial'
      
// }


    

}

@Component({
  selector: 'dialog-data-example-dialog',
  templateUrl: 'dialog-data-example-dialog.html',
  styleUrls: ['dialog-data-example-dialog.sass']
 })

 export class DialogOverviewExampleDialog {  
  constructor(
    public dialogRef: MatDialogRef<DialogOverviewExampleDialog>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) {}

    onNoClick(): void {
      this.dialogRef.close();
    }
 }
