import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { HttpHeaders } from '@angular/common/http';

const httpOptions = {
    headers: new HttpHeaders({
      'Content-Type':  'application/json',
    })
  };

@Injectable()



export class JsonCreate {


constructor(private http: HttpClient) { }

configUrl = 'https://jsonbox.io/box_ce820a7b8b65281a8753';
configDelete = "https://jsonbox.io/box_f9ba3926dade96693495?q=message:**"
getConfig() {
   
  return this.http.get(this.configUrl);
}

postJson(title,message,collection) {
    const data ={
      title,
      message,
      collection

    }
    console.log(data)
    return this.http.post(this.configUrl, data, httpOptions)
}

deleteData() {
  return this.http.delete(this.configDelete)
}


}

